        overflow: auto;
        padding-bottom: 30px;
    }
    .prof-bot__nav ul{
        display: flex;
    }
    .prof-bot__nav a{
        white-space: nowrap;
    }
    .prof_bot__title{
        margin-bottom: 20px;
    }
    .prof-bot__right{
        flex: auto;
        width: 100%;
        margin-top: 15px;
    }
}

