@media(max-width: 374px){
    .prof-bot__tbody .data{
        font-size: 13px;
    }
    .prof-bot__tbody .not-price{
        font-size: 13px;
    }
    .prof-bot__table .col-5{
        width: 20%;
    }
    .td-imgs .plus{
        font-size: 8px;
        height: 19px;
