    }
}
.profile-wrap{
    color: #fff;
    background-color: #203837;
    box-shadow: 0px 25px 20px rgb(0,0,0,0.1);
    padding: 30px;
    padding-bottom: 53px;
    background-image: url('../img/profile/fon.png');
    background-position: left -145px bottom -120px;
    background-repeat: no-repeat;
    .btn{
        font-weight: bold;
    }
}
.profile-wrap__text1{
    font-weight: bold;
    font-size: 24px;
    line-height: 110%;
    color: #FFFFFF;
    opacity: 0.6;
    margin-bottom: 10px;
    font-family: var(--font);
}

.profile-wrap__row{
    display: flex;
    .left{
        flex: 0 0 285px;
        margin-right: 78px;
    }
    .right{
        flex: 1;
    }
}

.profile-wrap__title1{
    font-weight: bold;
    font-size: 52px;
    line-height: 110%;
    font-family: var(--font);
    margin-bottom: 35px;
}
.profile-wrap__title2{
    font-weight: bold;
    font-size: 52px;
    line-height: 110%;
    font-family: var(--font);
    margin-bottom: 3px;
    span{
        font-size: 24px;

    }
}
.profile-wrap__text2{
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    margin-top: 8px;
    max-width: 500px;
}
.profile-form__block{
    padding-bottom: 10px;
    border-bottom:  1px solid #6E7D81;
    padding-top: 30px;
}
.profile-form__row{
    display: flex;
    flex-wrap: wrap;
}
.profile-form__col{
    margin-bottom: 15px;
    margin-right: 20px;
    min-width: 121px;
}
.profile-form__col--big{
    width: calc(45% - 42px);
    margin-right: 42px;
}
.profile-form__col:last-child{
    margin-right: 0;
}
.profile-form label{
    font-weight: 500;
    font-size: 16px;
    line-height: 110%;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #A9A9B5;
    margin-bottom: 13px;
    display: block;
}
.profile-form__text{
    font-weight: bold;
    font-size: 24px;
    line-height: 110%;
    font-family: var(--font);
    color: #FFFFFF;
}
.profile-form{
    overflow: hidden;
}
.profile-form .btn-reset{
    font-weight: bold;
    font-size: 18px;
    line-height: 110%;
    text-align: center;
    letter-spacing: 0.2em;
    color: #FCFCFC;
    opacity: 0.5;
    font-family: var(--font);
    background-color: transparent;
    border: none;
    padding: 5px 0;
    margin-top: 40px;
    transition: 0.3s all;
}
.profile-form .btn-reset:hover{
    opacity: 1;
    color: #fff;
}

@media(max-width: 1400px){
    .profile-form__col--big {
        width: calc(45% - 30px);
        margin-right: 30px;
    }
    .profile-wrap__row .left{
        margin-right: 30px;
    }
    .profile-form__col{
        width: calc(100% / 2);
        margin-right: 0;
        padding-right: 20px;
    }
}
@media(max-width: 1200px){
    .profile-wrap__row{
        flex-direction: column;
        .left{
            width: 100%;
            flex: auto;
            margin-right: 0;
            margin-bottom: 20px;
        }
        .right{
            width: 100%;
            flex: auto;
        }
    }
}

@media(max-width: 575px){
    .profile-form__col{
        width: 100%;
        padding-right: 0;
    }
    .profile-wrap{
        padding: 15px;
        padding-bottom: 40px;
    }
    .profile-wrap__title1{
        margin-bottom: 25px;
        font-size: 38px;
    }
    .profile-wrap__text1{
        font-size: 22px;
    }
    .profile-wrap__title2{
        font-size: 38px;
        span{
            font-size: 20px;
        }
    }
    .profile-wrap__row .left{
        margin-bottom: 40px;
    }
    .profile-form label{
        margin-bottom: 7px;
        font-size: 14px;
    }
    .profile-form__col{
        margin-bottom: 20px;
    }
    .prof .btn{
        width: 100%;
        font-size: 14px;
    }
    .profile-form__text{
        font-size: 20px;
    }
    .profile-form .btn-reset{
        display: block;
        margin: 0 auto;
        margin-top: 40px;
    }
}